<template>
  <div class="SubScribe py-16 text-white">
    <v-container>
      <v-form>
        <div class="subscribe-content">
          <h2>Subscribe For New Features</h2>
          <p class="lead mb-6">
            Stay updated with our latest features and improvements by
            subscribing to our newsletter.
          </p>
          <v-row>
            <v-col cols="12" md="8" lg="6">
              <v-text-field
                v-model="email"
                label="Enter your email"
                type="email"
                variant="outlined"
                clearable
                class="rounded-input"
              >
                <!-- Tooltip for email input -->
                <template v-slot:prepend>
                  <v-tooltip location="bottom">
                    <template v-slot:activator="{ props }">
                      <v-icon v-bind="props" icon="mdi-help-circle-outline" />
                    </template>
                    Enter a valid email address to subscribe.
                  </v-tooltip>
                </template>

                <!-- Subscribe button -->
                <template v-slot:append-inner>
                  <v-progress-circular
                    v-if="loading"
                    color="info"
                    size="24"
                    indeterminate
                  />
                  <v-btn v-else color="primary" class="ml-2" @click="subscribe">
                    Subscribe
                  </v-btn>
                </template>
              </v-text-field>
            </v-col>
          </v-row>
        </div>
      </v-form>
    </v-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      email: "", // Stores the email input
      loading: false, // Indicates if a subscription request is in progress
    };
  },
  methods: {
    subscribe() {
      if (!this.email || !this.validateEmail(this.email)) {
        alert("Please enter a valid email address.");
        return;
      }
      this.loading = true;
      setTimeout(() => {
        alert(`Subscription successful for ${this.email}`);
        this.email = "";
        this.loading = false;
      }, 2000); // Simulate an API request
    },
    validateEmail(email) {
      const regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return regex.test(email);
    },
  },
};
</script>

