<template>
  <v-app>
    <!-- App Bar -->
    <v-app-bar :elevation="20">
      <v-app-bar-title>
        <span style="color: #f73594">Best</span>
        <span style="color: #8d3e93">App</span>
      </v-app-bar-title>

      <!-- Desktop Navigation -->
      <template v-if="!isMobile">
        <v-list class="d-flex align-center" style="color: #95398c">
          <v-list-item
            v-for="item in items"
            :key="item.value"
            @click="navigateTo(item.value)"
            class="nav-item"
          >
            {{ item.title }}
          </v-list-item>
        </v-list>
      </template>

      <!-- Buttons -->

      <v-btn icon="mdi-heart" style="color: #95398c"></v-btn>
      <v-btn icon="mdi-magnify" style="color: #95398c"></v-btn>

      <!-- Drawer Toggle Button (For Mobile) -->
      <v-btn
        class="d-md-none"
        icon="mdi-dots-vertical"
        style="color: #95398c"
        @click.stop="drawer = !drawer"
      ></v-btn>
    </v-app-bar>

    <!-- Navigation Drawer (Mobile Only) -->
    <v-navigation-drawer v-model="drawer" app temporary right>
      <v-list>
        <v-list-item
          v-for="item in items"
          :key="item.value"
          @click="navigateTo(item.value)"
        >
          {{ item.title }}
        </v-list-item>
      </v-list>
    </v-navigation-drawer>

    <!-- Main Content -->
    <v-main>
      <section id="home">
        <HomeView />
      </section>
      <section id="features">
        <AppFeature />
      </section>
      <section id="about">
        <AboutApp />
      </section>
      <section id="subscribe">
        <SubScribe />
      </section>
      <section id="reviews">
        <PeopleReview />
      </section>
      <section id="pricing">
        <OurPrices />
      </section>
      <section id="contact">
        <ContactUs />
      </section>
    </v-main>

    <!-- Footer -->
    <FooterPart />
  </v-app>
</template>

<script>
import ContactUs from "@/components/ContactUs.vue";
import OurPrices from "@/components/OurPrices.vue";
import PeopleReview from "@/components/PeopleReview.vue";
import SubScribe from "@/components/SubScribe.vue";
import AboutApp from "@/components/AboutApp.vue";
import AppFeature from "@/components/AppFeature.vue";
import HomeView from "@/components/HomeView.vue";
import FooterPart from "@/components/FooterPart.vue";

export default {
  data: () => ({
    drawer: false,
    items: [
      { title: "Home", value: "home" },
      { title: "Features", value: "features" },
      { title: "About", value: "about" },
      { title: "Pricing", value: "pricing" },
      { title: "Contact", value: "contact" },
    ],
  }),
  computed: {
    isMobile() {
      return this.$vuetify?.breakpoint?.smAndDown ?? false;
    },
  },
  methods: {
    navigateTo(sectionId) {
      this.drawer = false;

      // Find the target section by ID
      const target = document.getElementById(sectionId);
      if (target) {
        // Scroll smoothly to the section
        target.scrollIntoView({
          behavior: "smooth",
          block: "start",
        });
      }
    },
  },
  components: {
    ContactUs,
    OurPrices,
    PeopleReview,
    SubScribe,
    AboutApp,
    AppFeature,
    HomeView,
    FooterPart,
  },
};
</script>

<style lang="scss">
/* General Styles */
.v-main {
  padding-top: 0;
  background-color: #f8f9fa; /* Optional background */
}
.nav-item {
  cursor: pointer;
  margin: 0 10px;
  font-weight: bold;
}
.nav-item:hover {
  color: #f73594;
}

/* Mobile-specific styles */
@media (max-width: 960px) {
  .v-list {
    display: flex;
    flex-direction: column;
    align-items: start;
  }
}

/* Section-specific styles */
section {
  padding: 0px 10px; /* Add padding for visibility */
}
</style>
